
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    title: String,
    image: String,
    description: String,
    url: String,
  },
  methods: {
    open: function() {
      window.open(`https://${this.url}`)
    }
  }
})

export default class Project extends Vue {
  title!: string;
  image!: string;
  description!: string;
  url!: string;
}

