<template>
  <div class="work" v-on:click="open">
    <img class="image" :src="require(`../assets/${image}.jpg`)">
    <h2 class="company">{{company}}</h2>
      <!-- <img class="link" src="../assets/arrow.svg"> -->
    <p class="title">{{title}}</p>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    title: String,
    image: String,
    company: String,
    url: String,
  },
  methods: {
    open: function() {
      window.open(`https://${this.url}`)
    }
  }
})

export default class Work extends Vue {
  title!: string;
  image!: string;
  company!: string;
  url!: string;
}

</script>

